            word: '在反复推荐了多次之后，泰国热门电影《天才枪手》终于在内地上映了，而这几天，微博和朋友圈也是铺天盖地的《天才枪手》的文章。 而桃桃观影团，也在周五晚，做了一场非常有趣的《天才枪手》中泰联合观影活动。 不过，提到《天才枪手》这部影片，很多文章似乎还是围绕在作弊与抄袭这些话题上。包括还有些人在由之探讨作弊技巧。 事实上，虽然这是一部关于作弊，或是以作弊为话题的电影。但是，仅仅将视野放置于作弊这件事上，未免也有点太小看这部电影了。 实际上，我们之前，也有很多影视作品，有关于作弊的描写。为什么，独独这部会受欢迎，并让人印象深刻呢？ 所以，重要的其实并非选题，而是如何去传达你的选题，这才是影片的亮点。 这也是我之前反复在说的，《天才枪手》的高明之处，在于将一部校园题材，处理成动作片或侠盗片。在影片所有的起承转折中，导演把学生考试作弊拍成了雄心勃勃、有风险的间谍惊悚片气质，就好像《十一罗汉》。 影片在节奏上做的很出色，尤其是高潮段落中，一个学校高层主管在学校和地铁站追逐琳恩的戏码，就像警察和坏人一样的“猫捉老鼠”，甚至会使人联想起《法国贩毒网》里的地铁追踪戏。这一刻，我们哪里是在看校园题材，明明是犯罪大片。 就像男女主去悉尼考试那场戏，考前从洗手间出来，镜头是仰拍，两人手握铅笔的样子，根本不是握笔的动作，而是武侠片，侠客握刀的手势，正式以这样的细节处理，来强化影片动作片的属性。 影片借助相当动作片节奏感的剪辑，以及极其出色且带节奏的配乐，让整部影片在情绪上充满律动感和紧张感，你能切身体会到角色在考场上的紧张与刺激。这才是影片出色的地方。 就像我们反复说的，选对了故事题材仅是第一步。更重要的是，要以娴熟的手法，恰当的方式把故事讲出来，才是真的成功。 看完这个故事，你会觉得够帅、很酷，这个才厉害了。 就好像其中有一场恶搞乔布斯苹果发布会，会非常带感。 具有现实意义的是，影片借助不同阶层身份的四个主要角色，构筑了他们之间的矛盾冲突，所有的情感都巧妙地呈现在银幕上，并且直言不讳地在最后上升到泰国的教育体系和社会阶层问题。 腐败的学校制度给高级公民带来了好处，赋予他们无限的特权和选择。低阶层公民则有许多限制，他们想要改变自己，实现跃升，只能铤而走险，从而引发了这样的作弊事件的发生。 就好像很多争议的男二黑化问题，同样是因为阶层壁垒，让他更加清楚的认识到，想要改变，似乎只有这一条路可以走，这才是让人绝望的。 最重要的是，编导用希区柯克擅长的“罪孽转障法”，另观众明知道主角做的是不道德的事，还是会为他们揪一把心。 说到影片技巧，还是很多有趣的地方的，比如女主角琳恩在开场、中段、结尾共出现了三次“镜渊”，也暗示了一步错步步错的无底洞。 “镜渊”来自法语“mise en abyme”。也是一种分身的表现形式，顾名思义，就是如深渊般的镜中镜，仿佛是个无底洞。其中第三次的“镜渊”是背景虚化，而此时的琳恩已经逐渐得到了救赎。 所以，影片真正的成功，还是得益于导演娴熟的技巧，这个，可绝不仅是拍个抄袭那么简单的事情了。 桃姐：这是我第三次看《天才枪手》，我是非常喜欢这个片子，我觉得看看人家的青春片拍到什么程度了，不止是青春题材的，也融入了很多其他的元素，比如他们最后考试的时候，两个人从第一次进洗手间出来的时候，音乐起的时候是一个仰拍的镜头，非常高大，握笔的姿势好像是在拿刀，很像武侠片那种感觉，用了非常动作片手法。 你仔细回忆这里面很多的剪辑和处理的方式，都是用的犯罪题材的方式，人家在青春片的基础上探索出了更多的可能性，不像我们日常看到的青春片可能是还谈恋爱、谈恋爱、谈恋爱的那种。 它已经开始在探讨其他的问题，不光是讲抄袭，其实也在讲泰国社会阶层之间的差距，不同阶层的人将会面对什么样的问题。其实他讲的是成人的问题，不只是青春的问题了。这次我也很高兴能请两位嘉宾，一位是来自泰国的朋友，另外一位也是很了解泰国文化的朋友，包括我们还邀请到了泰国的留学生，希望大家一起看这部片子，一方面是从国内观众的角度，一方面是从泰国方面的角度来聊这部电影。 另外，我们还聊到一些有趣的话题，比如： #100万泰铢=名牌大学毕业3年薪水 桃姐：电影里男孩临时要加价100万泰铢，这么多钱在泰国大概是什么概念？ 代修远：如果你是泰国挡住人，如果是曼谷当地人在相当于曼谷类似北京二环内买两层小楼总面积300平米到400平米，就是500万泰铢。 一个刚毕业的学生，从全泰国最好的大学毕业后，进到一家非常好的跨国公司，一个月的月薪差不多在人民币5000元到6000元左右也就是泰铢2至3万左右。所以我们看到这个男孩用他挣来的钱换了新买了很多洗衣机，买了小的机车，把店面装修了一下，应该还剩下很多钱。 #度众生还是度自己？ 提问：我想问一下，我看完之后我会觉得这个男主角他最后的一个转变有点太突兀了，我会觉得他前面铺垫的这个人物的造型不是一个会这么容易转变成太阴暗的人物，你会不会觉得男主角最后的转变有一点不是很完美？ 桃姐：其实结尾的转变不光你，很多人都有疑问，包括他的转变，那个女孩最后为什么要去告密。 我觉得，首先的原因在于，这个片子最后一定要转到一个很正向的东西上，通过一些人的变化最终刺激那个女孩，决定要转回到正面的选择。这个特别强的刺激，就是那个男孩的变化。一开始暗示了很多，如果你没有钱的话你可能面对的是另外一个情况，你可能在这个社会阶层里一辈子都是这个阶层里停留，你永远不会到另外一个阶层，你要很快的来到另外一个阶层的话，你只能使用这种方式。 而且其实在那里面对成人世界的描写是挺黑暗的，他们的父母都是很穷苦，学校的校长是反面角色，包括老师其实也是反面角色，第一场监考老师就说了原来辅导班的题都是教过的，你会发现那个成人世界其实蛮黑暗的，这些年轻人如何从他们的世界过渡到成人的世界里。最终他可能觉得我只能用这种方式，最后他说了一段话，如果你不跟我一起做这件事，我就把所有人都搞掉。在我看，女孩最后的选择源自爱。 代修远：另外我想补充一点看法，大家知道泰国信佛教，是小乘佛教，中国大部分人信的是大乘佛教。有什么区别呢？唐僧取经是为了救众生，普度众生是大乘佛教。小乘佛教是度自己。 所以他有这么一个转变的时候，他可能找到了一种渡自己的办法，他觉得通过这样的方式能让自己的生活过的更好。但他同时也会说到你这样做会威胁更多的人，或者让所有泰国学生的成绩都取消了，但是那个女孩依然决定要渡自己，要让自己觉得自己是清白的。这也是泰国佛教对于他们自己的影响。 坦白说，代修远的这段话，对我也是一个启发。可能，这也是不同地域的文化差异吧。如果从度自己的角度，确实很多事情都更顺了。',
